<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <div class="temp-item-inlineblock">
        <el-select v-model="form.state" filterable placeholder="请选择">
          <el-option v-for="item in stateList" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-input v-model="form.account_name" placeholder="账号名" clearable></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-input v-model="form.qq" placeholder="QQ" clearable></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="getMarketing_Account">查询</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <el-button type="primary" @click="newAccount">新建账号</el-button>
      </div>
    </div>
    <div class="table-wrap">
      <el-table :data="tableData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column prop="account_name" label="账号名称" sortable width="180">
        </el-table-column>
        <el-table-column prop="qq" label="QQ" sortable width="180">
        </el-table-column>
        <el-table-column prop="account_id" label="广点通账户ID" sortable width="180">
        </el-table-column>
        <el-table-column prop="grant_type" label="授权状态" sortable width="180">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p> {{ scope.row.message }}</p>
              <div slot="reference" class="name-wrapper">
                {{ scope.row.grant_type }}
                <i class="el-icon-question"></i>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column prop="OFF" label="账号状态" sortable width="180">
          <template slot-scope="scope">
            <el-switch @change="handleListStatusChange(scope.row)" v-model="scope.row.state === 'ON' ? true : false" active-color="#13ce66" inactive-color="#999">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="create_time" label="创建时间" sortable width="180">
        </el-table-column>
        <el-table-column prop="" label="操作">
          <template slot-scope="scope">
            <el-button @click="handleEditClick(scope.row.id)" type="text" size="small">编辑</el-button>
            <span v-if="scope.row.grant_type=='待授权'">
              <a class="target-href" :href="scope.row.grant_url" target="_blank">授权</a>
            </span>
            <span v-else>
              <a class="target-href" :href="scope.row.grant_url" target="_blank">重新授权</a>
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>

  </div>

</template>

<script>
import bus from '../../util/bus'

export default {
  created() {
    this.getMarketing_Account() //获取账户列表
  },
  data() {
    return {
      form: {
        account_name: '',
        qq: '',
        state: ''
      },
      stateList: [
        {
          value: '',
          label: '所有状态'
        },
        {
          value: 'ON',
          label: '开启'
        },
        {
          value: 'OFF',
          label: '关闭'
        }
      ],
      tableData: []
    }
  },
  methods: {
    // 新建账号
    newAccount() {
      this.$router.push('/setting/accountmanage/new')
    },
    // 操作 -- 编辑
    handleEditClick(id) {
      this.$router.push('/setting/accountmanage/edit/' + id)
    },

    // 设置账号状态
    handleListStatusChange(row) {
      var oldstate = row.state
      if (row.state === 'OFF') {
        row.state = 'ON'
      } else {
        row.state = 'OFF'
      }
      var params = {
        id: row.id,
        state: row.state
      }
      this.handleListStatusChangeRequest(row, params, oldstate)
    },
    // 修改账号状态 -- 请求
    handleListStatusChangeRequest(row, params, oldstate) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Account/changeState', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            //修改成功
            bus.$emit('updataAccounts') // 通知组件重新请求账号
          } else {
            row.state = oldstate
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          row.state = oldstate
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    // 获取账户列表
    getMarketing_Account() {
      var that = this
      this.form.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Account/get', {
          params: this.form
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            // console.log(respData.data)
            that.tableData = respData.data
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>
.target-href {
  font-size: 12px;
  color: #409eff;
  cursor: pointer;
  text-decoration: none;
}
</style>



